window.addEventListener('DOMContentLoaded', () => {
    fix100vh();
    window.addEventListener('resize', () => {
        fix100vh();
        getScrollBarSize()
    })

    getScrollBarSize()

    let localeRu = {
        days: ['Воскресенье', 'Понедельник', 'Вторник', 'Среда', 'Четверг', 'Пятница', 'Суббота'],
        daysShort: ['Вос', 'Пон', 'Вто', 'Сре', 'Чет', 'Пят', 'Суб'],
        daysMin: ['Вс', 'Пн', 'Вт', 'Ср', 'Чт', 'Пт', 'Сб'],
        months: ['Январь', 'Февраль', 'Март', 'Апрель', 'Май', 'Июнь', 'Июль', 'Август', 'Сентябрь', 'Октябрь', 'Ноябрь', 'Декабрь'],
        monthsShort: ['Янв', 'Фев', 'Мар', 'Апр', 'Май', 'Июн', 'Июл', 'Авг', 'Сен', 'Окт', 'Ноя', 'Дек'],
        today: 'Сегодня',
        clear: 'Отмена',
        dateFormat: 'dd.MM.yyyy',
        timeFormat: 'HH:mm',
        firstDay: 1
    };

    /**
     * https://stackoverflow.com/questions/2945113/how-to-create-a-new-date-in-javascript-from-a-non-standard-date-format
     * @param input
     * @returns {Date}
     */
    function parseDate(input) {
        if(!input){
            return '';
        }
        const parts = input.match(/(\d+)/g);
        return new Date(parts[2], parts[1]-1, parts[0]);
    }

    let dateInputs = document.querySelectorAll('.date-input input')
    dateInputs.forEach(el => {
        new AirDatepicker(el, {
            locale: localeRu,
            autoClose: true,
            toggleSelected: false,
            minDate: '',
            maxDate: parseDate(el.getAttribute('data-max-date')),
            selectedDates: [parseDate(el.value)],
            onSelect: () => {
                $(".date-input").addClass("input-active")
            },
            buttons: [
                'clear',
                {
                    content: 'Применить',
                    attrs: {
                        class: 'button-apply large'
                    },
                    onClick: (dp) => {
                        // Здесь ваша логика для кнопки "Применить"
                        console.log('Выбранная дата:', dp.selectedDates);
                        dp.hide(); // Скрываем календарь
                    }
                },
                {
                    content: 'Сегодня',
                    attrs: {
                        class: 'datepicker-today'
                    },
                    onClick: (dp) => {
                        let today = new Date();
                        dp.selectDate(today);
                        dp.setViewDate(today);
                    }
                },
            ],
            onShow: function(dp, animationCompleted){
                if (!animationCompleted) {
                    el.classList.add("_disabled")
                }
            },
            onHide: function(dp, animationCompleted){
                if (!animationCompleted) {
                    el.classList.remove("_disabled")
                }
            }
        })
    })

})

function fix100vh() {
    let vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);
}
