let dealSteps = document.querySelector(".deal-progress")

if (dealSteps) {
    let activeStep = document.querySelector('.deal-step._active')
    activeStep.scrollIntoView({
        behavior: "smooth",
        inline: "center",
        block: 'nearest',
    })
}

$(".verification-card2__btn").click(function () {
    $(this).parents(".verification-card2").toggleClass("_selected")
    let isDisabled = $(".verification-nav2__btn").prop("disabled")
    $(".verification-nav2__btn").prop("disabled",!isDisabled)
})
