$(document).on('click', '._tab', function(e) {
    let parentBlock = $(this).parents("._tabs-parent")
    let tabId = $(this).attr("data-tab")
    $(parentBlock).find("._tab").removeClass("_active")
    $(this).addClass("_active")
    $(parentBlock).find(".tab-content").removeClass("_active")
    $(parentBlock).find(`.${tabId}`).addClass("_active")
})

function setLabelPlace(){
    let fields = document.querySelectorAll(".fg input,.fg textarea")

    fields.forEach(field => {
        setLabelPosition(field)

        field.onchange = ()=> {
            setLabelPosition(field)
        }

        function setLabelPosition() {
            if(field.value !== ""){
                field.classList.add("_filled")
            } else{
                field.classList.remove("_filled")
            }
        }
    })
}


setLabelPlace()

$(".currency-select").click(function() {
    if($(this).parents(".exchange-item").hasClass("_open")){
        $(".exchange-item").removeClass("_open")
        if(window.innerWidth<=576){
            $("body").removeClass("fixed-body")
            $("body").removeClass("_open-currency-modal")
        }
    } else{
        $(".exchange-item").removeClass("_open")
        $(this).parents(".exchange-item").addClass("_open")
        if(window.innerWidth<=576){
            $("body").addClass("fixed-body")
            $("body").addClass("_open-currency-modal")
        }
    }
})


$("._close-currency-modal").click(function() {
    $(".exchange-item").removeClass("_open")
    $("body").removeClass("fixed-body")
    $("body").removeClass("_open-currency-modal")
})

function setLabelPlace(){
    let fields = document.querySelectorAll(".fg input,.fg textarea,.fg-dynamic select")

    fields.forEach(field => {
        setLabelPosition(field)

        field.onchange = ()=> {
            setLabelPosition(field)
        }

        function setLabelPosition() {
            if(field.value !== ""){
                field.classList.add("_filled")
            } else{
                field.classList.remove("_filled")
            }
        }
    })
}


setLabelPlace()

$("[data-copy]").click(function () {
    let text = $(this).attr("data-copy")
    let $tmp = $("<textarea>");
    $("body").append($tmp);
    $tmp.val(text).select();
    document.execCommand("copy");
    $tmp.remove();

    $(this).parents(".m-copy").addClass("_open")

    setTimeout(() => {
        $(this).parents(".m-copy").removeClass("_open")
    }, 5000);
})

$(".pass-toggle").click(function () {
    if($(this).hasClass("_active")){
        $(this).removeClass("_active")
        $(this).siblings("input").attr("type","password")
    } else{
        $(this).addClass("_active")
        $(this).siblings("input").attr("type","text")
    }
})
